'use client';

import { useState, useRef, useEffect, ChangeEvent, FormEvent, DragEvent } from 'react';
import Link from 'next/link';
import Sidebar from '../components/Sidebar';
import { useTheme } from '../context/ThemeContext';
import * as XLSX from 'xlsx';

export default function EskiVeriYukle() {
  const { theme } = useTheme();
  const [selectedMonth, setSelectedMonth] = useState('');
  const [selectedPeriod, setSelectedPeriod] = useState('');
  const [fileName, setFileName] = useState('');
  const [isFileSelected, setIsFileSelected] = useState(false);
  const [excelDataBySirket, setExcelDataBySirket] = useState<{[key: string]: any[]}>({});
  const [showData, setShowData] = useState(false);
  const fileInputRef = useRef<HTMLInputElement>(null);
  const [isSaving, setIsSaving] = useState(false);
  const [sirketler, setSirketler] = useState<{[key: string]: {id: string, ad: string}}>({});
  const [sirketlerYukleniyor, setSirketlerYukleniyor] = useState(true);

  // Veritabanƒ±ndan ≈üirket bilgilerini √ßekme
  useEffect(() => {
    const fetchSirketler = async () => {
      try {
        setSirketlerYukleniyor(true);
        const response = await fetch('/api/sirketler');
        
        if (!response.ok) {
          throw new Error('≈ûirket verileri √ßekilemedi');
        }
        
        const data = await response.json();
        
        // ≈ûirketleri ID'ye g√∂re map'leyelim
        const sirketMap: {[key: string]: {id: string, ad: string}} = {};
        data.forEach((sirket: any) => {
          const sirketId = String(sirket.sirket_id);
          sirketMap[sirketId] = {
            id: sirketId,
            ad: sirket.sirket_adi || `≈ûirket ${sirketId}`
          };
        });
        
        setSirketler(sirketMap);
      } catch (error) {
        console.error('≈ûirket verileri √ßekilirken hata olu≈ütu:', error);
        
        // Hata durumunda varsayƒ±lan ≈üirket isimleri kullanƒ±lacak
        setSirketler({
          '1': { id: '1', ad: '≈ûirket 1' },
          '2': { id: '2', ad: '≈ûirket 2' },
          '3': { id: '3', ad: '≈ûirket 3' }
        });
      } finally {
        setSirketlerYukleniyor(false);
      }
    };
    
    fetchSirketler();
  }, []);

  const months = [
    'Ocak', '≈ûubat', 'Mart', 'Nisan', 'Mayƒ±s', 'Haziran',
    'Temmuz', 'Aƒüustos', 'Eyl√ºl', 'Ekim', 'Kasƒ±m', 'Aralƒ±k'
  ];

  const periods = [
    { id: '1', name: '1-10' },
    { id: '2', name: '11-20' },
    { id: '3', name: '21-Ay Sonu' }
  ];

  const handleFileChange = (e: ChangeEvent<HTMLInputElement>) => {
    if (e.target.files && e.target.files.length > 0) {
      setFileName(e.target.files[0].name);
      setIsFileSelected(true);
      setShowData(false);
    } else {
      setFileName('');
      setIsFileSelected(false);
      setShowData(false);
    }
  };

  const handleSubmit = async (e: FormEvent<HTMLFormElement>) => {
    e.preventDefault();
    
    if (!isFileSelected || !fileInputRef.current?.files?.[0]) {
      alert('L√ºtfen bir dosya se√ßin');
      return;
    }
    
    const file = fileInputRef.current.files[0];
    const reader = new FileReader();
    
    reader.onload = (evt) => {
      try {
        if (!evt.target?.result) return;
        
        const binaryStr = evt.target.result;
        const workbook = XLSX.read(binaryStr, { type: 'binary' });
        
        // Her ≈üirket i√ßin ayrƒ± veriler
        const dataGroupedBySirket: {[key: string]: any[]} = {};
        
        // Her sayfa i√ßin d√∂ng√º
        workbook.SheetNames.forEach((sheetName, sheetIndex) => {
          // Sayfa indeksine g√∂re ≈üirket id'sini belirle (1-tabanlƒ±)
          const sirketId = String(sheetIndex + 1);
          
          const worksheet = workbook.Sheets[sheetName];
          const rawData = XLSX.utils.sheet_to_json(worksheet, { defval: null });
          
          // Her satƒ±ra ≈üirket id'si ekle
          const dataWithCompanyId = rawData.map((row: any) => ({
            ...row,
            sirket_id: sirketId
          }));
          
          // Verileri ≈üirket ID'sine g√∂re gruplandƒ±r
          const processedData = processExcelData(dataWithCompanyId);
          
          // ≈ûirket ID'sine g√∂re veri kaydet
          dataGroupedBySirket[sirketId] = processedData;
        });
        
        setExcelDataBySirket(dataGroupedBySirket);
        setShowData(true);
      } catch (error) {
        console.error('Excel dosyasƒ± okunurken hata olu≈ütu:', error);
        alert('Excel dosyasƒ± okunurken bir hata olu≈ütu.');
      }
    };
    
    reader.readAsBinaryString(file);
  };

  // Excel verilerini seferlere g√∂re i≈üleme fonksiyonu
  const processExcelData = (rawData: any[]) => {
    const seferler: any[] = [];
    let currentSefer: any = null;
    
    rawData.forEach((row) => {
      // Sƒ±ra No s√ºtunu varsa ve deƒüeri dolu ise, yeni sefer ba≈ülat
      const firstColumnKey = Object.keys(row)[0];
      
      if (row[firstColumnKey] !== null && row[firstColumnKey] !== undefined && row[firstColumnKey] !== '') {
        // Yeni sefer
        if (currentSefer) {
          seferler.push(currentSefer);
        }
        
        currentSefer = {
          seferNo: row[firstColumnKey],
          duraklar: [row],
          tumVeriler: { ...row } // Ham verilerin tam kopyasƒ±
        };
      } else if (currentSefer) {
        // Mevcut sefere yeni durak ekle
        currentSefer.duraklar.push(row);
        
        // Eƒüer durakta bo≈ü olmayan deƒüerler varsa, bunlarƒ± tumVeriler'e ekle
        Object.keys(row).forEach(key => {
          if (row[key] !== null && row[key] !== undefined && row[key] !== '') {
            // √ñnceki deƒüer var mƒ± kontrol et
            if (currentSefer.tumVeriler[key] === undefined || 
                currentSefer.tumVeriler[key] === null || 
                currentSefer.tumVeriler[key] === '') {
              // √ñnceki deƒüer yoksa yeni deƒüeri ekle
              currentSefer.tumVeriler[key] = row[key];
            } else {
              // √ñnceki deƒüer varsa, aynƒ± deƒüer mi kontrol et
              if (String(currentSefer.tumVeriler[key]) !== String(row[key])) {
                // ƒ∞ki deƒüeri birle≈ütir - aralarƒ±na " - " ekle
                if (!String(currentSefer.tumVeriler[key]).includes(String(row[key]))) {
                  const combinedValue = `${currentSefer.tumVeriler[key]} - ${row[key]}`;
                  currentSefer.tumVeriler[key] = combinedValue;
                }
              }
            }
          }
        });
      }
    });
    
    // Son seferi ekle
    if (currentSefer) {
      seferler.push(currentSefer);
    }
    
    return seferler;
  };

  // Bir tablo i√ßin s√ºtun verilerini getirme fonksiyonu
  const getColumnValue = (sefer: any, columnName: string) => {
    // ƒ∞lk sefer i√ßin debug bilgisi
    if (sefer.seferNo === 1 && columnName === '≈û√∂f√∂r') {
      console.log('Excel s√ºtun adlarƒ±:', Object.keys(sefer.tumVeriler || {}));
      console.log('Sefer verileri:', sefer.tumVeriler);
    }
    
    // ≈û√∂f√∂r i√ßin √∂zel i≈ülem - ara√ß ID'sini √ßƒ±kar ve g√∂ster
    if (columnName === '≈û√∂f√∂r') {
      let soforDegeri = '';
      
      // √ñncelikle Excel'deki orijinal veriyi kontrol et
      if (sefer.tumVeriler && sefer.tumVeriler[columnName] !== undefined && 
          sefer.tumVeriler[columnName] !== null && 
          sefer.tumVeriler[columnName] !== '') {
        soforDegeri = sefer.tumVeriler[columnName];
      } else {
        // Kƒ±smƒ± e≈üle≈üme i√ßin ≈ü√∂f√∂r s√ºtunu ara
        const soforMatchingKey = Object.keys(sefer.tumVeriler || {}).find(k => {
          const lowerK = k.toLowerCase();
          return lowerK.includes('≈ü√∂f√∂r') || 
                 lowerK.includes('≈üof√∂r') || 
                 lowerK.includes('sof√∂r') || 
                 lowerK.includes('sofor') ||
                 lowerK.includes('driver') ||
                 lowerK.includes('s√ºr√ºc√º');
        });
        
        if (soforMatchingKey && sefer.tumVeriler[soforMatchingKey] !== null && 
            sefer.tumVeriler[soforMatchingKey] !== undefined && 
            sefer.tumVeriler[soforMatchingKey] !== '') {
          soforDegeri = sefer.tumVeriler[soforMatchingKey];
        }
      }
      
      // ≈û√∂f√∂r deƒüeri varsa, ara√ß ID'sini vurgula
      if (soforDegeri) {
        // ID pattern'i kontrol et
        const idPattern = /[\(\[\{#]?\s*(?:ID|id|Id)?:?\s*(\d+)\s*[\)\]\}]?|[\-‚Äì]\s*(\d+)\s*$|#\s*(\d+)/;
        const idMatch = soforDegeri.match(idPattern);
        
        if (idMatch) {
          const extractedId = idMatch[1] || idMatch[2] || idMatch[3];
          // ID'yi vurgulu g√∂ster
          const cleanName = soforDegeri
            .replace(/[\(\[\{].*?[\)\]\}]/g, '')
            .replace(/[\-‚Äì]\s*\d+\s*$/g, '')
            .replace(/\s*ID:?\s*\d+/gi, '')
            .replace(/#\s*\d+/g, '')
            .trim();
          
          return `${cleanName} [Ara√ß ID: ${extractedId}]`;
        }
      }
      
      return soforDegeri;
    }
    
    // Ta≈üƒ±ma Fiyatƒ± i√ßin √∂zel i≈ülem
    if (columnName === 'Birim Fiyatƒ±') {
      return getTasimaFiyati(sefer);
    }
    
    // Ara√ß Tipi i√ßin √∂zel i≈ülem - MT deƒüerine g√∂re belirle
    if (columnName === 'Ara√ß Tipi') {
      // √ñnce MT deƒüerini al
      let mtValue = null;
      
      // MT s√ºtununu bul
      if (sefer.tumVeriler && sefer.tumVeriler['MT'] !== undefined && 
          sefer.tumVeriler['MT'] !== null && 
          sefer.tumVeriler['MT'] !== '') {
        mtValue = sefer.tumVeriler['MT'];
      } else {
        // Kƒ±smƒ± e≈üle≈üme i√ßin MT s√ºtunu ara
        const mtMatchingKey = Object.keys(sefer.tumVeriler || {}).find(k => 
          k.toLowerCase().includes('mt') || 
          k.toLowerCase().includes('tonaj') ||
          k.toLowerCase().includes('aƒüƒ±rlƒ±k') ||
          k.toLowerCase().includes('agirlik')
        );
        
        if (mtMatchingKey && sefer.tumVeriler[mtMatchingKey] !== null && 
            sefer.tumVeriler[mtMatchingKey] !== undefined && 
            sefer.tumVeriler[mtMatchingKey] !== '') {
          mtValue = sefer.tumVeriler[mtMatchingKey];
        }
      }
      
      // MT deƒüeri varsa buna g√∂re ara√ß tipini belirle
      if (mtValue !== null && mtValue !== undefined && mtValue !== '') {
        try {
          // MT deƒüerini sayƒ±ya √ßevir
          const mtNumeric = parseFloat(mtValue.toString().replace(/[^\d.,]/g, '').replace(',', '.'));
          
          if (!isNaN(mtNumeric)) {
            if (mtNumeric < 23) {
              return 'Kƒ±rkayak';
            } else {
              return 'Tƒ±r';
            }
          }
        } catch (e) {
          console.error('MT deƒüeri i≈ülenirken hata:', e);
        }
      }
      
      // MT deƒüeri yoksa veya i≈ülenemediyse, Excel'den direkt Ara√ß Tipi deƒüerini al
      if (sefer.tumVeriler && sefer.tumVeriler[columnName] !== undefined && 
          sefer.tumVeriler[columnName] !== null && 
          sefer.tumVeriler[columnName] !== '') {
        return sefer.tumVeriler[columnName];
      }
      
      // Kƒ±smƒ± e≈üle≈üme i√ßin Ara√ß Tipi s√ºtunu ara
      const aracTipiMatchingKey = Object.keys(sefer.tumVeriler || {}).find(k => 
        k.toLowerCase().includes('ara√ß') && k.toLowerCase().includes('tip') ||
        k.toLowerCase().includes('arac') && k.toLowerCase().includes('tip') ||
        k.toLowerCase().includes('vehicle') && k.toLowerCase().includes('type')
      );
      
      if (aracTipiMatchingKey && sefer.tumVeriler[aracTipiMatchingKey] !== null && 
          sefer.tumVeriler[aracTipiMatchingKey] !== undefined && 
          sefer.tumVeriler[aracTipiMatchingKey] !== '') {
        return sefer.tumVeriler[aracTipiMatchingKey];
      }
      
      return '';
    }
    
    // ƒ∞rsaliye Tarihi i√ßin √∂zel i≈ülem
    if (columnName === 'ƒ∞rsaliye Tarihi') {
      // √ñncelikle Excel'deki orijinal veriyi kontrol et
      let tarihDegeri = '';
      
      if (sefer.tumVeriler && sefer.tumVeriler[columnName] !== undefined && 
          sefer.tumVeriler[columnName] !== null && 
          sefer.tumVeriler[columnName] !== '') {
        tarihDegeri = sefer.tumVeriler[columnName];
      } else {
        // Kƒ±smƒ± e≈üle≈üme i√ßin (ba≈ülƒ±k farklƒ± olabilir)
        const matchingKey = Object.keys(sefer.tumVeriler || {}).find(k => 
          k.toLowerCase().includes(columnName.toLowerCase())
        );
        
        if (matchingKey && sefer.tumVeriler[matchingKey] !== null && 
            sefer.tumVeriler[matchingKey] !== undefined && 
            sefer.tumVeriler[matchingKey] !== '') {
          tarihDegeri = sefer.tumVeriler[matchingKey];
        }
      }
      
      // Eƒüer tarih deƒüeri varsa, GG AA YYYY formatƒ±na √ßevir
      if (tarihDegeri) {
        try {
          // Tarih formatƒ±nƒ± kontrol et ve d√∂n√º≈üt√ºr
          const cleanDateStr = tarihDegeri.toString().trim();
          
          // Nokta veya slash ile ayrƒ±lmƒ±≈ü tarih formatƒ±
          const dateParts = cleanDateStr.split(/[\/\.]/);
          if (dateParts.length >= 3) {
            const day = parseInt(dateParts[0], 10);
            const month = parseInt(dateParts[1], 10);
            let year = parseInt(dateParts[2], 10);
            
            // 2 haneli yƒ±l kontrol√º
            if (year < 100) {
              year += 2000; // 23 -> 2023
            }
            
            // Ge√ßerli tarih deƒüerleri kontrol√º
            if (!isNaN(day) && !isNaN(month) && !isNaN(year) && 
                day >= 1 && day <= 31 && month >= 1 && month <= 12) {
              // GG AA YYYY formatƒ±na d√∂n√º≈üt√ºr
              return `${day < 10 ? '0' + day : day} ${month < 10 ? '0' + month : month} ${year}`;
            }
          }
          
          // Eƒüer d√∂n√º≈üt√ºrme yapƒ±lamazsa, orijinal deƒüeri d√∂nd√ºr
          return tarihDegeri;
        } catch (e) {
          console.error('Tarih d√∂n√º≈üt√ºrme hatasƒ±:', e);
          return tarihDegeri;
        }
      }
      
      return '';
    }
    
    // Diƒüer s√ºtunlar i√ßin normal i≈ülem
    // √ñncelikle Excel'deki orijinal veriyi kontrol et
    if (sefer.tumVeriler && sefer.tumVeriler[columnName] !== undefined && 
        sefer.tumVeriler[columnName] !== null && 
        sefer.tumVeriler[columnName] !== '') {
      return sefer.tumVeriler[columnName];
    }
    
    // Kƒ±smƒ± e≈üle≈üme i√ßin (ba≈ülƒ±k farklƒ± olabilir)
    let matchingKey = Object.keys(sefer.tumVeriler || {}).find(k => 
      k.toLowerCase().includes(columnName.toLowerCase())
    );
    
    // ≈û√∂f√∂r i√ßin √∂zel arama - daha esnek
    if (!matchingKey && columnName.toLowerCase().includes('≈ü√∂f√∂r')) {
      matchingKey = Object.keys(sefer.tumVeriler || {}).find(k => {
        const lowerK = k.toLowerCase();
        return lowerK.includes('≈ü√∂f√∂r') || 
               lowerK.includes('≈üof√∂r') || 
               lowerK.includes('sof√∂r') || 
               lowerK.includes('sofor') ||
               lowerK.includes('driver') ||
               lowerK.includes('s√ºr√ºc√º');
      });
      
      // ≈û√∂f√∂r √úcreti i√ßin ek kontrol
      if (columnName.toLowerCase().includes('√ºcret') && !columnName.toLowerCase().includes('fatura')) {
        matchingKey = Object.keys(sefer.tumVeriler || {}).find(k => {
          const lowerK = k.toLowerCase();
          return (lowerK.includes('≈ü√∂f√∂r') || lowerK.includes('≈üof√∂r') || lowerK.includes('sof√∂r') || lowerK.includes('sofor')) &&
                 (lowerK.includes('√ºcret') || lowerK.includes('ucret')) &&
                 !lowerK.includes('fatura');
        });
      }
      
      // ≈û√∂f√∂r Fatura √úcreti i√ßin ek kontrol
      if (columnName.toLowerCase().includes('fatura')) {
        matchingKey = Object.keys(sefer.tumVeriler || {}).find(k => {
          const lowerK = k.toLowerCase();
          return (lowerK.includes('≈ü√∂f√∂r') || lowerK.includes('≈üof√∂r') || lowerK.includes('sof√∂r') || lowerK.includes('sofor')) &&
                 lowerK.includes('fatura');
        });
      }
    }
    
    if (matchingKey && sefer.tumVeriler[matchingKey] !== null && 
        sefer.tumVeriler[matchingKey] !== undefined && 
        sefer.tumVeriler[matchingKey] !== '') {
      return sefer.tumVeriler[matchingKey];
    }
    
    return '';
  };

  // Ta≈üƒ±ma fiyatƒ±nƒ± alma fonksiyonu
  const getTasimaFiyati = (sefer: any) => {
    if (!sefer.tumVeriler) return '';
    
    // √ñnce Ton/Fiyat s√ºtununu bul
    let tonFiyatValue = null;
    
    // Tam e≈üle≈üme ara
    if (sefer.tumVeriler['Ton/Fiyat']) {
      tonFiyatValue = sefer.tumVeriler['Ton/Fiyat'];
    } else {
      // Kƒ±smi e≈üle≈üme ara
      const matchingKey = Object.keys(sefer.tumVeriler).find(k => {
        const lowerKey = k.toLowerCase();
        return (lowerKey.includes('ton') && lowerKey.includes('fiyat')) ||
               lowerKey.includes('ton/fiyat') ||
               lowerKey.includes('ton-fiyat') ||
               lowerKey.includes('birim') && lowerKey.includes('fiyat');
      });
      if (matchingKey) {
        tonFiyatValue = sefer.tumVeriler[matchingKey];
      }
    }
    
    // Ton/Fiyat deƒüeri varsa onu kullan
    if (tonFiyatValue !== null && tonFiyatValue !== undefined && tonFiyatValue !== '') {
      const valueStr = tonFiyatValue.toString().trim();
      
      // Eƒüer deƒüer birle≈ütirilmi≈üse (√∂rn: "0 - 21888", "13600 - 113468")
      if (valueStr.includes(' - ')) {
        const parts = valueStr.split(' - ');
        
        // Sƒ±fƒ±r olmayan ilk deƒüeri bul
        for (const part of parts) {
          const cleanPart = part.trim();
          const numValue = parseFloat(cleanPart.replace(/[^\d.,]/g, '').replace(',', '.'));
          
          if (!isNaN(numValue) && numValue > 0) {
            return cleanPart;
          }
        }
        
        // Hi√ß sƒ±fƒ±r olmayan deƒüer yoksa bo≈ü d√∂nd√ºr
        return '';
      }
      
      // Tek deƒüerse, sƒ±fƒ±r deƒüilse g√∂ster
      const numValue = parseFloat(valueStr.replace(/[^\d.,]/g, '').replace(',', '.'));
      if (!isNaN(numValue) && numValue > 0) {
        return valueStr;
      }
    }
    
    // Ton/Fiyat bulunamazsa, genel ta≈üƒ±ma fiyatƒ± s√ºtunlarƒ±nƒ± ara
    const fiyatKeys = Object.keys(sefer.tumVeriler).filter(key => 
      key.toLowerCase().includes('ta≈üƒ±ma') && key.toLowerCase().includes('fiyat') ||
      key.toLowerCase().includes('tasima') && key.toLowerCase().includes('fiyat') ||
      key.toLowerCase().includes('birim') && key.toLowerCase().includes('fiyat') ||
      key.toLowerCase().includes('transport') && key.toLowerCase().includes('price')
    );
    
    if (fiyatKeys.length > 0) {
      const fiyatValue = sefer.tumVeriler[fiyatKeys[0]];
      
      if (fiyatValue !== null && fiyatValue !== undefined && fiyatValue !== '') {
        const valueStr = fiyatValue.toString().trim();
        
        // Eƒüer deƒüer birle≈ütirilmi≈üse
        if (valueStr.includes(' - ')) {
          const parts = valueStr.split(' - ');
          
          // Sƒ±fƒ±r olmayan ilk deƒüeri bul
          for (const part of parts) {
            const cleanPart = part.trim();
            const numValue = parseFloat(cleanPart.replace(/[^\d.,]/g, '').replace(',', '.'));
            
            if (!isNaN(numValue) && numValue > 0) {
              return cleanPart;
            }
          }
          
          return '';
        }
        
        // Tek deƒüerse, sƒ±fƒ±r deƒüilse g√∂ster
        const numValue = parseFloat(valueStr.replace(/[^\d.,]/g, '').replace(',', '.'));
        if (!isNaN(numValue) && numValue > 0) {
          return valueStr;
        }
      }
    }
    
    return '';
  };

  // ≈ûirket adƒ±nƒ± ID'den alma fonksiyonu
  const getSirketAdi = (sirketId: string) => {
    if (sirketler[sirketId]) {
      return sirketler[sirketId].ad;
    }
    return `≈ûirket ${sirketId}`;
  };

  // Tablo olu≈üturma komponenti
  const TableForCompany = ({ sirketId, data }: { sirketId: string, data: any[] }) => {
    const sirketAdi = getSirketAdi(sirketId);
    
    return (
      <div className="bg-white p-6 rounded-lg border border-gray-200 w-full mb-8">
        <h2 className="text-2xl font-bold mb-6 text-center text-gray-900">{sirketAdi}</h2>
        <div className="overflow-x-auto">
          <table className="min-w-full bg-white border border-gray-200 rounded-lg">
            <thead>
              <tr className="bg-gray-50">
                <th className="px-4 py-2 text-left border-b border-gray-200 w-16 text-gray-700 text-sm">Sƒ±ra No</th>
                <th className="px-4 py-2 text-left border-b border-gray-200 w-28 text-gray-700 text-sm">ƒ∞rsaliye Tarihi</th>
                <th className="px-4 py-2 text-left border-b border-gray-200 w-28 text-gray-700 text-sm">ƒ∞rsaliye Numarasƒ±</th>
                <th className="px-4 py-2 text-left border-b border-gray-200 w-32 text-gray-700 text-sm">√áƒ±kƒ±≈ü Yeri</th>
                <th className="px-4 py-2 text-left border-b border-gray-200 w-40 text-gray-700 text-sm">Tahliye Edilen Firma</th>
                <th className="px-4 py-2 text-left border-b border-gray-200 w-32 text-gray-700 text-sm">Tahliye Yeri/Tesisi</th>
                <th className="px-4 py-2 text-left border-b border-gray-200 w-24 text-gray-700 text-sm">Tonaj/Kg</th>
                <th className="px-4 py-2 text-left border-b border-gray-200 w-28 text-gray-700 text-sm">Ara√ß Tipi</th>
                <th className="px-4 py-2 text-left border-b border-gray-200 w-16 text-gray-700 text-sm">MT</th>
                <th className="px-4 py-2 text-left border-b border-gray-200 w-24 text-gray-700 text-sm">Birim Fiyatƒ±</th>
                <th className="px-4 py-2 text-left border-b border-gray-200 w-32 text-gray-700 text-sm">≈û√∂f√∂r</th>
                <th className="px-4 py-2 text-left border-b border-gray-200 w-24 text-gray-700 text-sm">≈û√∂f√∂r √úcreti</th>
                <th className="px-4 py-2 text-left border-b border-gray-200 w-28 text-gray-700 text-sm">≈û√∂f√∂r Fatura √úcreti</th>
              </tr>
            </thead>
            <tbody>
              {data.map((sefer, index) => {
                const siraNo = sefer.seferNo;
                
                return (
                  <tr key={index} className={index % 2 === 0 ? 'bg-white' : 'bg-gray-50'}>
                    <td className="px-4 py-2 border-b border-gray-200 text-gray-900 text-sm">{siraNo}</td>
                    <td className="px-4 py-2 border-b border-gray-200 text-gray-900 text-sm">{getColumnValue(sefer, 'ƒ∞rsaliye Tarihi')}</td>
                    <td className="px-4 py-2 border-b border-gray-200 text-gray-900 text-sm">{getColumnValue(sefer, 'ƒ∞rsaliye Numarasƒ±')}</td>
                    <td className="px-4 py-2 border-b border-gray-200 text-gray-900 text-sm">{getColumnValue(sefer, '√áƒ±kƒ±≈ü Yeri')}</td>
                    <td className="px-4 py-2 border-b border-gray-200 text-gray-900 text-sm">{getColumnValue(sefer, 'Tahliye Edilen Firma')}</td>
                    <td className="px-4 py-2 border-b border-gray-200 text-gray-900 text-sm">{getColumnValue(sefer, 'Tahliye Yeri/Tesisi')}</td>
                    <td className="px-4 py-2 border-b border-gray-200 text-gray-900 text-sm">{getColumnValue(sefer, 'Tonaj/Kg')}</td>
                    <td className="px-4 py-2 border-b border-gray-200 text-gray-900 text-sm">{getColumnValue(sefer, 'Ara√ß Tipi')}</td>
                    <td className="px-4 py-2 border-b border-gray-200 text-gray-900 text-sm">{getColumnValue(sefer, 'MT')}</td>
                    <td className="px-4 py-2 border-b border-gray-200 text-gray-900 text-sm">{getColumnValue(sefer, 'Birim Fiyatƒ±')}</td>
                    <td className="px-4 py-2 border-b border-gray-200 text-gray-900 text-sm">{getColumnValue(sefer, '≈û√∂f√∂r')}</td>
                    <td className="px-4 py-2 border-b border-gray-200 text-gray-900 text-sm">{getColumnValue(sefer, '≈û√∂f√∂r √úcreti')}</td>
                    <td className="px-4 py-2 border-b border-gray-200 text-gray-900 text-sm">{getColumnValue(sefer, '≈û√∂f√∂r Fatura √úcreti')}</td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    );
  };

  const browseFile = () => {
    if (fileInputRef.current) {
      fileInputRef.current.click();
    }
  };

  const handleDragOver = (e: DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();
  };

  const handleDrop = (e: DragEvent<HTMLDivElement>) => {
    e.preventDefault();
    e.stopPropagation();
    
    if (e.dataTransfer.files && e.dataTransfer.files.length > 0) {
      const file = e.dataTransfer.files[0];
      
      // Excel dosyasƒ± kontrol√º
      if (file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' || 
          file.type === 'application/vnd.ms-excel') {
        
        // fileInputRef'i g√ºncelle ki handleSubmit fonksiyonu bu dosyayƒ± okuyabilsin
        const dataTransfer = new DataTransfer();
        dataTransfer.items.add(file);
        
        if (fileInputRef.current) {
          fileInputRef.current.files = dataTransfer.files;
        }
        
        setFileName(file.name);
        setIsFileSelected(true);
        setShowData(false);
        
        // Otomatik olarak dosyayƒ± g√∂r√ºnt√ºle
        const reader = new FileReader();
        
        reader.onload = (evt) => {
          try {
            if (!evt.target?.result) return;
            
            const binaryStr = evt.target.result;
            const workbook = XLSX.read(binaryStr, { type: 'binary' });
            
            // Her ≈üirket i√ßin ayrƒ± veriler
            const dataGroupedBySirket: {[key: string]: any[]} = {};
            
            // Her sayfa i√ßin d√∂ng√º
            workbook.SheetNames.forEach((sheetName, sheetIndex) => {
              // Sayfa indeksine g√∂re ≈üirket id'sini belirle (1-tabanlƒ±)
              const sirketId = String(sheetIndex + 1);
              
              const worksheet = workbook.Sheets[sheetName];
              const rawData = XLSX.utils.sheet_to_json(worksheet, { defval: null });
              
              // Her satƒ±ra ≈üirket id'si ekle
              const dataWithCompanyId = rawData.map((row: any) => ({
                ...row,
                sirket_id: sirketId
              }));
              
              // Verileri ≈üirket ID'sine g√∂re gruplandƒ±r
              const processedData = processExcelData(dataWithCompanyId);
              
              // ≈ûirket ID'sine g√∂re veri kaydet
              dataGroupedBySirket[sirketId] = processedData;
            });
            
            setExcelDataBySirket(dataGroupedBySirket);
            setShowData(true);
          } catch (error) {
            console.error('Excel dosyasƒ± okunurken hata olu≈ütu:', error);
            alert('Excel dosyasƒ± okunurken bir hata olu≈ütu.');
          }
        };
        
        reader.readAsBinaryString(file);
      } else {
        alert('L√ºtfen Excel dosyasƒ± (.xlsx veya .xls) se√ßin');
      }
    }
  };

  const handleSaveData = async () => {
    if (!selectedMonth || !selectedPeriod) {
      alert('L√ºtfen ay ve d√∂nem se√ßin');
      return;
    }

    if (!excelDataBySirket || Object.keys(excelDataBySirket).length === 0) {
      alert('Kaydedilecek veri bulunamadƒ±');
      return;
    }

    setIsSaving(true);

    try {
      const response = await fetch('/api/eski-veri-yukle', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
        },
        body: JSON.stringify({
          ay: months.indexOf(selectedMonth) + 1,
          donem: parseInt(selectedPeriod),
          seferler: excelDataBySirket
        }),
      });

      if (response.ok) {
        const result = await response.json();
        
        // Ba≈üarƒ± mesajƒ±nƒ± g√∂ster
        let mesaj = `Ba≈üarƒ±lƒ±! ${result.message}`;
        
        // Eƒüer hatalar varsa, detaylarƒ± g√∂ster
        if (result.hatalar && result.hatalar.length > 0) {
          mesaj += '\n\nHata Detaylarƒ±:\n';
          result.hatalar.forEach((hata: string, index: number) => {
            mesaj += `${index + 1}. ${hata}\n`;
          });
        }
        
        alert(mesaj);
        setShowData(false);
        setIsFileSelected(false);
        setFileName('');
        setSelectedMonth('');
        setSelectedPeriod('');
        setExcelDataBySirket({});
        if (fileInputRef.current) {
          fileInputRef.current.value = '';
        }
      } else {
        const errorData = await response.json();
        alert(`Hata: ${errorData.error || 'Bilinmeyen hata'}`);
      }
    } catch (error) {
      console.error('Veri kaydedilirken hata olu≈ütu:', error);
      alert('Veri kaydedilirken bir hata olu≈ütu');
    } finally {
      setIsSaving(false);
    }
  };

  return (
    <div className="flex min-h-screen bg-gray-50">
      {/* Sol kenar men√ºs√º */}
      <Sidebar />
      
      {/* Ana i√ßerik alanƒ± */}
      <div className="flex-1 p-8">
        <div className="max-w-7xl mx-auto">
          {/* √úst navigation */}
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-3xl font-bold text-gray-900">Eski Veri Y√ºkle</h1>
            <Link 
              href="/"
              className="bg-blue-600 text-white px-4 py-2 rounded-md hover:bg-blue-700 transition-colors"
            >
              ‚Üê Ana Men√º
            </Link>
          </div>
          
          {/* Dosya y√ºkleme formu */}
          <div className="bg-white rounded-lg shadow-lg p-6 mb-8">
            <h2 className="text-xl font-semibold mb-6 text-gray-900">Excel Dosyasƒ± Y√ºkle</h2>
            <p className="text-sm text-gray-600 mb-4">
              Excel dosyasƒ±nda 3 sayfa olmalƒ±dƒ±r: 1. sayfa ≈ûirket 1, 2. sayfa ≈ûirket 2, 3. sayfa ≈ûirket 3 i√ßin seferler.
            </p>
            
            <form onSubmit={handleSubmit} className="space-y-6">
              {/* Ay ve D√∂nem Se√ßimi */}
              <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    Ay Se√ßin
                  </label>
                  <select
                    value={selectedMonth}
                    onChange={(e) => setSelectedMonth(e.target.value)}
                    className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                    required
                  >
                    <option value="">Ay se√ßin...</option>
                    {months.map((month, index) => (
                      <option key={index} value={month}>
                        {month}
                      </option>
                    ))}
                  </select>
                </div>
                
                <div>
                  <label className="block text-sm font-medium text-gray-700 mb-2">
                    D√∂nem Se√ßin
                  </label>
                  <select
                    value={selectedPeriod}
                    onChange={(e) => setSelectedPeriod(e.target.value)}
                    className="w-full p-3 border border-gray-300 rounded-lg focus:ring-2 focus:ring-blue-500 focus:border-blue-500"
                    required
                  >
                    <option value="">D√∂nem se√ßin...</option>
                    {periods.map((period) => (
                      <option key={period.id} value={period.id}>
                        {period.name}
                      </option>
                    ))}
                  </select>
                </div>
              </div>

              {/* Dosya Y√ºkleme Alanƒ± */}
              <div
                className="border-2 border-dashed border-gray-300 rounded-lg p-8 text-center hover:border-blue-400 transition-colors cursor-pointer"
                onDragOver={handleDragOver}
                onDrop={handleDrop}
                onClick={browseFile}
              >
                <div className="space-y-4">
                  <div className="text-4xl text-gray-400">üìÅ</div>
                  <div>
                    <p className="text-lg font-medium text-gray-700">
                      {fileName || 'Excel dosyasƒ±nƒ± buraya s√ºr√ºkleyin veya tƒ±klayƒ±n'}
                    </p>
                    <p className="text-sm text-gray-500 mt-2">
                      Desteklenen formatlar: .xlsx, .xls
                    </p>
                  </div>
                </div>
                
                <input
                  ref={fileInputRef}
                  type="file"
                  accept=".xlsx,.xls"
                  onChange={handleFileChange}
                  className="hidden"
                />
              </div>

              {/* Dosya G√∂r√ºnt√ºle Butonu */}
              <div className="flex justify-center">
                <button
                  type="submit"
                  disabled={!isFileSelected}
                  className="px-6 py-3 bg-blue-600 text-white rounded-lg hover:bg-blue-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors"
                >
                  Dosyayƒ± G√∂r√ºnt√ºle
                </button>
              </div>
            </form>
          </div>

          {/* Veri √ñnizleme */}
          {showData && Object.keys(excelDataBySirket).length > 0 && (
            <div className="bg-white rounded-lg shadow-lg p-6">
              <div className="flex justify-between items-center mb-6">
                <h3 className="text-xl font-semibold text-gray-900">
                  Veri √ñnizleme ({Object.values(excelDataBySirket).reduce((total, seferler) => total + seferler.length, 0)} toplam sefer)
                </h3>
                <button
                  onClick={handleSaveData}
                  disabled={isSaving}
                  className="px-6 py-3 bg-green-600 text-white rounded-lg hover:bg-green-700 disabled:bg-gray-400 disabled:cursor-not-allowed transition-colors"
                >
                  {isSaving ? 'Kaydediliyor...' : 'Verileri Kaydet'}
                </button>
              </div>

              {/* Her ≈üirket i√ßin tablo */}
              {Object.entries(excelDataBySirket).map(([sirketId, seferler]) => (
                <TableForCompany key={sirketId} sirketId={sirketId} data={seferler} />
              ))}
            </div>
          )}
        </div>
      </div>
    </div>
  );
} 